}
.kiwi-workspace::before,
.kiwi-workspace::after {
  position: absolute;
  content: '';
  left: 0;
  right: auto;
  margin-top: -4px;
  width: 100%;
  height: 7px;
  z-index: 0;
  transition: width 0.3s;
}
.kiwi-workspace::after {
  right: 0;
  left: auto;
  width: 0;
}
.kiwi-workspace--disconnected::before {
  width: 0;
}
.kiwi-workspace--disconnected::after {
  width: 100%;
}
.kiwi-workspace-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
